.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: #222222;
  color: #F6F1FB;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
  border:1px solid #F6F1FB;
}
.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #DC143C;
  color: #FBFBFB;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.modal__header h2 {
  margin: 0.5rem;
}
.modal__content {
  padding: 1rem 0.5rem;
}
.modal__footer {
  padding: 1rem 0.5rem;
}
@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}
.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}
.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}
.modal-exit {
  transform: translateY(0);
  opacity: 1;
}
.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}