.profile-list {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  width: 90%;
  max-width: 40rem;
}

.profile-item {
  margin: 1rem 0;
}
.profile-item__content {
  padding: 0;
}
.profile-item__info {
  padding: 1rem;
  text-align: center;
  background:#222222;
  color: #F6F1FB;
}
.profile-item__image {
  width: 100%;
  height: 12.5rem;
  margin-right: 1.5rem;
}
.profile-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.profile-item__info h2,
.profile-item__info h3,
.profile-item__info p {
  margin: 0 0 0.5rem 0;
}
.profile-item__actions {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #ccc;
  background:#222222;
}
.profile-item__modal-content {
  padding: 0;
}
.profile-item__modal-actions {
  text-align: right;
}
.profile-item__actions button,
.profile-item__actions a {
  margin: 0.5rem;
}
.map-container {
  height: 15rem;
  width: 100%;
}
@media (min-width: 768px) {
  .profile-item__image {
    height: 20rem;
  }
}